<template>
    <div id="backgroundImages">
        <div class="login">
            <div style="text-align:center;margin: 10px;">微信步数</div>
            <el-form :model="wxForm" :rules="wxrules" ref="wxForm" label-width="100px">
                <el-form-item label="账号" prop="username">
                    <el-input v-model="wxForm.username" clearable placeholder="账号"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="wxForm.password" clearable placeholder="密码"></el-input>
                </el-form-item>
                <el-form-item label="步数" prop="step">
                    <el-input v-model="wxForm.step" clearable placeholder="步数"></el-input>
                </el-form-item>
                <el-form-item label="开启定时刷" prop="istime">
                    <el-radio-group v-model="wxForm.istime">
                        <el-radio :label="true">是</el-radio>
                        <el-radio :label="false">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="最小步数" prop="minstep" v-if="wxForm.istime">
                    <el-input v-model="wxForm.minstep" clearable placeholder="最小步数"></el-input>
                </el-form-item>
                <el-form-item label="最大步数" prop="maxstep" v-if="wxForm.istime">
                    <el-input v-model="wxForm.maxstep" clearable placeholder="最大步数"></el-input>
                </el-form-item>
                <el-form-item label="过期时间" prop="expiryTime" v-if="wxForm.istime">
                    <el-date-picker v-model="wxForm.expiryTime" type="date" value-format="yyyy-MM-dd"
                        placeholder="选择过期时间">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div style="margin-top:10px;text-align: center;">
                <el-button type="primary" :loading="goLoading" @click="GO('wxForm')">GO</el-button>
            </div>
            <div style="text-align:center;color:red;font-size: 12px;">每天13点 15点开刷</div>
        </div>
    </div>
</template>

<script>
import { Message } from 'element-ui';
import { updateStep } from '../axiosRequest/allRequest'
export default {
    data() {
        return {
            wxForm: {
                username: '',
                password: "",
                step: "",
                istime: false,
                minstep: '',
                maxstep: '',
                expiryTime: ''
            },
            wxrules: {
                username: [
                    { required: true, message: '请输入账号', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                ],
                step: [
                    { required: true, message: '请输入步数', trigger: 'blur' },
                ],
                istime: [],
                minstep: [],
                maxstep: [],
                expiryTime: [],
            },
            goLoading: false,
            bgcUrl: require('../assets/wxbs.jpg')
        }
    },

    created() {

    },

    methods: {
        GO(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.goLoading = true
                    updateStep(this.wxForm).then(res => {
                        this.goLoading = false
                        if (res.code != 200) {
                            Message.error(res.message)
                        } else {
                            Message.success(res.message)
                        }
                    })
                } else {
                    return false;
                }
            });
        }
    }
}
</script>

<style lang="less" scoped>
#backgroundImages {
    min-height: 600px;
    width: 100%;
    height: 100vh;
    background: url("../assets/wxbs.jpg");
    // background: var(--bgcUrl);
    background-size: 100% 100%;
    overflow: hidden;
}

.login {
    width: 30%;
    max-height: 80%;
    min-height: 50%;
    background-color: rgba(255, 255, 255, 0.5);
    margin: auto;
    margin-top: 5%;
    padding: 10px;
    border-radius: 10px;
}

::v-deep .el-input__inner {
    border: 1px solid black;

    &::-webkit-input-placeholder {
        color: rgb(98, 97, 97);
    }

    .el-input__suffix {
        color: black
    }
}

::v-deep .el-form-item__label {
    color: black
}
</style>