<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">系统参数</div>
      <div class="rightButton">
        <el-input
          placeholder="请输入名称/值/备注"
          v-model="code"
          clearable
          class="input-with-select"
        >
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="getList"
          ></el-button>
        </el-input>
        <el-button type="primary" size="mini" @click="openParamter('增加参数')"
          >增加参数</el-button
        >
      </div>
    </div>

    <div class="mainMseeage">
      <div class="ChooseAll" v-if="ShowChooseAll">
        <el-button type="primary" :loading="batchDeleteLoading" @click="batch()"
          >批量删除</el-button
        >
        <span style="margin-left: 10px"
          >已选择<span style="color: #409eff">{{ choosedNum }}</span
          >项</span
        >
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        :height="tableHeight"
        :loading="tableLoading"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column type="index" width="55" label="序号">
        </el-table-column>
        <el-table-column prop="code" label="参数编码" width="" align="center">
        </el-table-column>
        <el-table-column
          prop="codeName"
          label="参数名称"
          width="200"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column prop="value" label="参数值" width="" align="center">
        </el-table-column>
        <el-table-column
          prop="createdTime"
          label="新增时间"
          width=""
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="updatedTime"
          label="更新时间"
          width=""
          align="center"
        >
        </el-table-column>

        <el-table-column
          prop=""
          label="操作"
          width=""
          fixed="right"
          align="center"
        >
          <template slot-scope="scoped">
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              placement="top"
            >
              <el-button
                type="text"
                class="operationButton"
                @click="openParamter('修改参数', scoped.row)"
                ><i class="el-icon-edit" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>

            <!-- <el-tooltip class="item" effect="dark" content="启用" placement="top">
                            <el-button type="text" class="operationButton" @click="startEnv(scoped.row)"><i
                                    class="el-icon-circle-check" style="color: #409eff"></i>
                            </el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="禁用" placement="top">
                            <el-button type="text" class="operationButton" @click="forbiddenEnv(scoped.row)"><i
                                    class="el-icon-remove-outline" style="color: #409eff"></i>
                            </el-button>
                        </el-tooltip> -->

            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              placement="top"
            >
              <el-button
                type="text"
                :loading="scoped.row.deleteLoading"
                class="operationButton"
                @click="deleteEnv(scoped.row)"
                ><i class="el-icon-delete" style="color: #409eff"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="changePage" :page-sizes="[10, 20, 30]" :page-size="changeSize"
                layout="total, sizes, prev, pager, next, jumper" :total="tableTotal">
            </el-pagination> -->
    </div>

    <el-dialog
      :title="paramterName"
      :visible.sync="addVisible"
      width="30%"
      @closed="handleClose('addForm')"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addForm">
        <el-form-item label="参数编码" prop="code">
          <el-input
            v-model="addForm.code"
            :disabled="paramterName == '修改参数'"
            clearable
            placeholder="请输入参数编码"
          ></el-input>
        </el-form-item>
        <el-form-item label="参数名称" prop="codeName">
          <el-input
            v-model="addForm.codeName"
            clearable
            placeholder="请输入参数名称"
          ></el-input>
        </el-form-item>
        <el-form-item label="参数值" prop="value">
          <el-input
            v-model="addForm.value"
            clearable
            placeholder="请输入参数值"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button
          type="primary"
          :loading="addLoading"
          @click="addParamter('addForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  querySystemParams,
  addSystemParam,
  updateSystemParam,
  deleteSystemParams,
} from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      code: "",
      ShowChooseAll: false,
      // tableHeight: window.innerHeight,
      tableHeight: window.innerHeight - 120,
      changePage: 1,
      changeSize: 10,
      tableTotal: 0,
      choosedNum: 0,
      paramterName: "添加参数",
      addVisible: false,
      addLoading: false,
      tableData: [],
      tableLoading: false,
      addForm: {
        value: "",
        codeName: "",
        code: "",
      },
      addFormRules: {
        code: [{ required: true, message: "请输入参数编码", trigger: "blur" }],
        value: [{ required: true, message: "请输入参数值", trigger: "blur" }],
        codeName: [
          { required: true, message: "请输入参数名称", trigger: "blur" },
        ],
      },
      multipleSelection: [],
      batchDeleteLoading: false,
    };
  },
  created() {
    this.getList();
  },
  mounted() {},
  methods: {
    getList() {
      this.tableLoading = true;
      querySystemParams({ code: this.code }).then((res) => {
        this.tableLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          let a = res.data;
          a.forEach((item, index) => {
            this.$set(item, "deleteLoading", false);
          });
          this.tableData = a;
        }
      });
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.choosedNum = val.length;
      if (val.length > 0) {
        this.ShowChooseAll = true;
      } else {
        this.ShowChooseAll = false;
      }
    },
    handleSizeChange(val) {
      //一页多少条
    },
    handleCurrentChange(val) {
      //第几页
    },
    handleClose(formName) {
      this.$refs[formName].resetFields();
    },
    addParamter(val) {
      this.$refs[val].validate((valid) => {
        if (valid) {
          this.addLoading = true;
          if (this.paramterName == "修改参数") {
            updateSystemParam(this.addForm).then((res) => {
              this.addLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.addVisible = false;
                this.getList();
              }
            });
          } else if (this.paramterName == "增加参数") {
            addSystemParam(this.addForm).then((res) => {
              this.addLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.addVisible = false;
                this.getList();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    openParamter(val, row) {
      this.addVisible = true;
      this.paramterName = val;
      if (row) {
        this.addForm.code = row.code;
        this.addForm.codeName = row.codeName;
        this.addForm.value = row.value;
      }
    },

    batch() {
      let deleteId = [];
      this.multipleSelection.forEach((item) => {
        deleteId.push(item.id);
      });
      this.batchDeleteLoading = true;
      deleteSystemParams(deleteId).then((res) => {
        this.batchDeleteLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          Message.success(res.message);
          this.getList();
        }
      });
    },

    startEnv(row) {
      //启用
      this.$confirm("是否确认启用xxx?", "确认启用", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        Message.success("111");
      });
    },
    forbiddenEnv(row) {
      //禁用
      this.$confirm("是否确认禁用xxx?", "确认禁用", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        Message.success("222");
      });
    },
    deleteEnv(row) {
      //删除
      this.$confirm("是否确认删除此参数?", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.tableData.forEach((item) => {
          if (item.id == row.id) {
            this.$set(item, "deleteLoading", true);
          }
        });
        deleteSystemParams([row.id]).then((res) => {
          this.tableData.forEach((item) => {
            if (item.id == row.id) {
              this.$set(item, "deleteLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success(res.message);
            this.getList();
          }
        });
      });
    },
  },
};
</script>
    
    <style lang="less" scoped>
.el-radio-group {
  width: 100%;
}
</style>